




























import { Component, Vue, Prop } from "vue-property-decorator";
import { ITodo } from "@/interfaces/ITodo";

@Component
export default class AppTodo extends Vue {
  @Prop({ type: Object, default: () => ({}) }) todo!: ITodo;
  isEdit = false;

  get isCheck() {
    return {
      done: this.todo.isDone
    };
  }
}
